import { useState, useEffect } from 'react';
import { useStore } from 'react-redux';

export function useIsWindowOpen() {
  const store = useStore();
  const [isWindowOpen, setIsWindowOpen] = useState(store.getState());
  
  useEffect(() => {
    setIsWindowOpen(store.getState());
  }, [store.getState()]);

  return isWindowOpen;
}