import React from 'react';
import { useIsWindowOpen } from '../Hooks';

export default function ContactItem({ href, src, alt }) {
  let isWindowOpen  = useIsWindowOpen();

  return (
    <div className="contact-item">
      <a
        aria-hidden={isWindowOpen}
        tabIndex={isWindowOpen ? '-1' : '0'}
        target="_blank" 
        rel="noopener noreferrer"
        href={href}
      >
        <img src={src} alt={alt} />
      </a>
    </div>
  )
}