import React from 'react';
import { useIsWindowOpen } from '../Hooks';

export default function PortfolioItem({ openView, view, image, title, tags }) {
  let isWindowOpen  = useIsWindowOpen();
  const handleKeyPress = (e) => e.key === 'Enter' ? openView(view) : null;
  
  return(
    <div 
      className="portfolio-item"
      onClick={() => openView(view)}
      aria-hidden={isWindowOpen}
      tabIndex={isWindowOpen ? '-1' : '0'}
      role="button"
      onKeyPress={(e) => handleKeyPress(e)}
    >
      <div className="portfolio-item__img-wrapper">
        <div className="gradient-background"/>
        <img 
          src={image}
          alt={title}
        />
      </div>
      <div className="portfolio-item__description">
        <h3>{title}</h3>
        <p>{tags}</p>
      </div>
    </div>
  )
}