import React from 'react';
import { Switch, Route, useHistory, useRouteMatch } from "react-router-dom";
import { useDispatch } from 'react-redux';
import { setWindowIsOpen } from './store/actions';
import PortfolioItem from './components/PortfolioItem';
import WindowWrapper from './components/WindowWrapper';
import { Tabs, Project } from './Constants';
import MuffinView from './projects/Muffin';
import ForzaView from './projects/Forza';
import MasterThesisView from './projects/MasterThesis';
import muffinImage from './res/muffin.jpg';
import hockeyImage from './res/hockey.jpg';
import forzaImage from './res/Forza.png';

export default function Projects() {
    let history = useHistory();
    let { url, path } = useRouteMatch();
    const dispatch = useDispatch();

    const closeView = () => {
        history.push(Tabs.PORTFOLIO);
        dispatch(setWindowIsOpen(false));
    }

    const openView = (view) => {
        history.push(`${url}${view}`);
        dispatch(setWindowIsOpen(true));
        window.scrollTo(0, 0);
    }

    return (
        <div>
            <div className="projects">
                <div className="portfolio-items">
                    <PortfolioItem
                        openView={openView} 
                        view={Project.MASTER_THESIS} 
                        image={hockeyImage}
                        title={'Second screen application'}
                        tags={'UI/UX'}
                    />
                    <PortfolioItem 
                        openView={openView} 
                        view={Project.MUFFIN_CHIHUAHUA} 
                        image={muffinImage}
                        title={'Tangible neural network project'}
                        tags={'Physical installation'}
                    />
                    <PortfolioItem 
                        openView={openView} 
                        view={Project.FORZA} 
                        image={forzaImage}
                        title={'Forza Football'}
                        tags={'UI/UX'}
                    />
                </div>
            </div>
            <Switch>
                <Route path={`${path}${Project.MUFFIN_CHIHUAHUA}`}>
                    <WindowWrapper content={<MuffinView />} closeView={closeView} />
                </Route>
                <Route path={`${path}${Project.MASTER_THESIS}`}>
                    <WindowWrapper content={<MasterThesisView />} closeView={closeView} />
                </Route>
                <Route path={`${path}${Project.FORZA}`}>
                    <WindowWrapper content={<ForzaView />} closeView={closeView} />
                </Route>
            </Switch>
        </div>
    );
};