import React, { Fragment, useEffect, useState } from 'react';
import {
  Switch,
  Route,
  Link,
  useLocation
} from "react-router-dom";
import linkedInIcon from './res/linkedin.svg';
import gitHubIcon from './res/github.svg';
import mailIcon from './res/mail.svg';
import resumeIcon from './res/resume.svg';
import resume from './res/resume.pdf';
import Favicon from 'react-favicon';
import favIcon from './res/favicon.ico';
import ProjectsView from './Projects.js';
import AboutView from './About.js';
import ContactItem from './components/ContactItem';
import { Tabs } from './Constants.js';
import { useIsWindowOpen } from './Hooks';

export default function App() {
  const [activeTab, setActiveTab] = useState(Tabs.PORTFOLIO);
  let location = useLocation();
  let isWindowOpen  = useIsWindowOpen();
  document.title = "Portfolio | Mattias Johansson";

  useEffect(() => {
    if(location.pathname === Tabs.PORTFOLIO) {
      setActiveTab(Tabs.PORTFOLIO);
    } else if (location.pathname === Tabs.ABOUT) {
      setActiveTab(Tabs.ABOUT);
    }
  }, [location]);

  const activeTabStyle = {
    borderBottom: '0.2em solid #9b59b6'
  }

  const getTabStyle = (tab) => tab === activeTab ? activeTabStyle : null

  return (
    <Fragment>
      <Favicon url={favIcon} />
      <div 
        className="header" 
        aria-hidden={isWindowOpen}
        tabIndex={isWindowOpen ? '-1' : '0'}
      >
        <div className="header-wrapper">
          <div className="info-container">
            <div className="name-box">
              <h1>Mattias Johansson</h1>
              <div className="border-box"></div>
            </div>
            <div className="contact">
              <ContactItem
                href={resume}
                src={resumeIcon}
                alt="resume"
              />
              <ContactItem
                href="http://linkedin.com/in/mattias-johansson-19b770108"
                src={linkedInIcon}
                alt="linkedin"
              />
              <ContactItem
                href="http://github.com/MattiasJo"
                src={gitHubIcon}
                alt="github"
              />
              <ContactItem
                href="mailto:mattiasj81@gmail.com"
                src={mailIcon}
                alt="mail"
              />
            </div>
          </div>
          <div className="navigation">
            <Link 
              to={Tabs.ABOUT}
              aria-hidden={isWindowOpen}
              tabIndex={isWindowOpen ? '-1' : '0'}
              style={getTabStyle(Tabs.ABOUT)}
            >
              About me
            </Link>
            <Link 
              to={Tabs.PORTFOLIO}
              aria-hidden={isWindowOpen}
              tabIndex={isWindowOpen ? '-1' : '0'}
              style={getTabStyle(Tabs.PORTFOLIO)}
            >
              Portfolio
            </Link>
          </div>
        </div>
      </div>
      <Switch>
        <Route path={Tabs.ABOUT}>
          <AboutView />
        </Route>
        <Route path={Tabs.PORTFOLIO}>
          <ProjectsView />
        </Route>
        <Route path="/">
          <ProjectsView />
        </Route>
      </Switch>
    </Fragment>
  );
};